

















import Vue from 'vue';
// eslint-disable-next-line import/no-unresolved
import { PropValidator } from 'vue/types/options';

const QSwitch = {
  count: 0,
};

export const name = 'QSwitch';

export default Vue.extend({
  name,
  data() {
    return {
      // eslint-disable-next-line no-plusplus
      inputID: `${name}-${++QSwitch.count}`,
    };
  },

  model: {
    prop: 'checked',
    event: 'change',
  },

  inheritAttrs: false,

  computed: {
    QSwitchClasses(): object {
      const { size, flexDirection } = this;
      const classes = {
        'q-switch': true,
        [`q-switch--${size}`]: size,
        [`q-switch--${flexDirection}`]: flexDirection,
      };
      return classes;
    },
  },

  props: {
    checked: Boolean,
    label: String,
    flexDirection: {
      type: String,
      default: 'row',
    } as PropValidator<'' | 'row' | 'column' | 'row-reverse' | 'column-reverse'>,
    size: {
      type: String,
      default: '',
    } as PropValidator<'' | 'small' | 'large' | 'mini'>,
  },
});
